<template>
    <article>
        <ul>
            <transition-group name="listanimation" tag="div">
                <li v-for='(item,index) in articles' :key='item.id' :style="[index<4?{animationDelay: index/4+'s'}:'']">
                    <router-link :to="item.url">
                        <header class="ListTitle">{{ item.infotitle }}</header>
                        <span class="time">{{ item.time }}</span>
                        <p class="ListContent">{{ item.shortcontent }}</p>
                    </router-link>
                </li>
            </transition-group>
        </ul>
        <catalog v-on:setToNewes="getFromCatalog"></catalog>
    </article>
</template>

<script>
    import catalog from '../articles/Catalog'
    export default {
        name: 'blogbody',
        data () {
            return {
                articles: ""
            }
        },
        components: {
            catalog
        },
        methods: {
            getFromCatalog: function(CatalogValue) {
                this.articles = CatalogValue
            }
        }
    };
</script>
<style scoped>
    article{ background: #FFF; position: relative; }
    ul{ width: 800px; margin: 0 auto; padding: 40px 0; }
    li a{ position: relative; padding: 20px; margin-top: 40px; box-shadow: 0 0 4px 0 rgba(0,0,0,.2); z-index: 1; border-radius: 5px; }
    li:first-child a{ margin: 0; }
    i{ position: absolute; top: 0; left: 0; width: 100%; height: 180px; z-index: -99; border-radius: 5px 5px 0 0; }
    header{ font-size: 20px; font-weight: bold; }
    span{ padding-top: 5px; display: block; }
    p{ font-size: 15px; padding-top: 10px; line-height: 1.8em; max-height: 9em; overflow: hidden; text-overflow: ellipsis; display: -webkit-box;-webkit-line-clamp: 5;-webkit-box-orient: vertical; }

    @media (max-width: 800px){
        ul{ width: 100%; padding: 40px 15px; box-sizing: border-box; }
        li a{ padding: 15px; }
        header{ font-size: 18px; }
    }

    /*动画*/
    .listanimation-enter-active{
        animation: fadeFromBottom 1s both;
    }
</style>



